const Base = require('../base.js');
//修改管理员
module.exports = class extends Base {
    async indexAction() {
        const model = this.model('user_info');
        var sd_ddf = this.ctx.post(),
            s_xig_s = {}
        s_xig_s.touxiang = sd_ddf.touxiang
        s_xig_s.user_name = sd_ddf.user_name
        s_xig_s.user_paw = sd_ddf.user_paw
        s_xig_s.dengji = sd_ddf.dengji
        let affectedRows = await model.where({
            id: sd_ddf.id
        }).update(s_xig_s);

        var jhde = {}
        if (affectedRows == 1) {
            jhde.code = 0
            jhde.msg = "修改账号成功"


        } else {
            jhde.code = -1
            jhde.msg = "修改账号失败"
        }
        return this.fail(jhde); //2输出json 推荐

    }

};
